import React from "react";
import LogoSrc from "../../assets/far.gif";
import { styled } from "../../../../stitches.config";

export function NavBar(props) {
  return (
    <>
      <Nav>
        <div>
          <img src={LogoSrc} width="70px" height="70px" />
        </div>
        <div
          style={{
            color: "#d82b31",
            fontSize: "30px",
            flexGrow: 1,
            textAlign: "center",
            fontWeight: 500,
            fontFamily: "Century Gothic",
          }}
        >
          BETTER PLANET
        </div>
      </Nav>
    </>
  );
}

const Nav = styled("div", {
  height: "100%",
  padding: "10px 30px",
  background: "#1e1f1c",
  display: "flex",
  // justifyContent: "space-between",
  alignItems: "center",
});
