import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { CarModelPage } from "./pages/carModel/pages/main";

export function App(props) {
  return (
    <Router>
      <Routes>
        <Route path="/" exact element={<CarModelPage />} />
      </Routes>
    </Router>
  );
}
